import React from 'react'
import PropTypes from 'prop-types'

const Person = ({person}) => {
  return (
    <div>
      {person.firstname} - {person.lastname}
    </div>
  )
}

Person.propTypes = {
  person: PropTypes.object.isRequired
}

export default Person


